import Vue from 'vue';
import Vuex from 'vuex';
import { History } from '@/History';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    theRecord: {
      history: [] as History[],
    },
  },
  mutations: {
    initRecord(state, csv: string) {
      const table: string[][] = [];
      for (const [index, row] of csv.split('\r\n').entries()) {
        table[index] = [];

        const regex = /"([^"]+)"[,]?|([^,]+)[,]?/g;
        let result = regex.exec(row);
        let count = 0;
        while (result !== null) {
          table[index][count++] = result[1] === undefined ? result[2] : result[1];
          result = regex.exec(row);
        }
      }

      for (let i = 7; i < table.length - 1; i++) {
        const regex = /(\d+)\/(\d+)\/(\d+)/;
        const result = regex.exec(table[i][0]);

        state.theRecord.history.push({
          date: result === null ? new Date() : new Date(Number(result[3]), Number(result[1]), Number(result[2])),
          description: table[i][1],
          amount: Number(table[i][2]),
          category: undefined,
        });
      }
    },
  },
  actions: {

  },
});
